import React, { Component } from "react";
import gql from "graphql-tag";
import { client } from "../modules/apolloClient";
import Link from 'next/link'

const siteQuery = gql`
  query($siteUrl: String) {
    sites(where: { name: { equals: $siteUrl } }) {
      id
      language
      languageLabel
      name
      niche
      logotype {
        url
      }
      providers(first: 10) {
        id
        name
        rating
        slug
      }
    }
  }
`;

export default class IndexPage extends Component {
  static async getInitialProps(ctx) {
    let { data: {sites} } = await client.query({
      query: siteQuery,
      variables: {
        siteUrl: ctx.req.headers["wdn-site-url"]
      }
    });

    return { site: sites[0] };
  }

  render() {
    let {providers} = this.props.site
    return (
      <div>
        <h3>Providers</h3>
        {providers.map(provider => {
          return (
            <Link href={`/providers/${provider.slug}`} prefetch>
              {provider.name}\n
            </Link>
          )
        })}
        <pre>{JSON.stringify(this.props.site, null, 2)}</pre>
      </div>
    );
  }
}
